@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #000;
  color: #fff;
  font-family: "Anton", sans-serif;
  min-height: 100vh;
  width: 100%;
  position: relative;
}
.app__container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@media screen and (min-width: 1200px) {
  body {
    background: url(/static/media/hero-bg2.55393ba3.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

/* MAIN CONTAINER */
.navbar__container {
    height: 10vh;
    background-color: rgb(89, 55, 242);
    color: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 1rem;
    font-size: 1.5rem;
}
.navbar__logo {
    text-transform: uppercase;
    font-size: 1.5rem;
}
/* LINKS CONTAINER */
.navbar__links--container {
    display: flex;
}
/* SIDEBAR OPEN/CLOSE BUTTONS */
.sidebar__toggle--btn {
    display: none;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    transition: 0.3s;
}
.sidebar__toggle--btn:hover {
    transform: scale(1.1);
}
/* RESPONSIVE NAVBAR */
@media screen and (max-width: 500px){
    .navbar__links--container {
        display: none;
    }
    .sidebar__toggle--btn {
        display: block;
    }
}
.navbar__link {
    font-size: 1.2rem;
    margin-right: 1rem;
    cursor: pointer;
    text-transform: uppercase;
}
.navbar__link:hover, .navbar__link:active{
    transform: scale(1.1);
    color: #fff;
}
.hidden {
    display: none;
}
.hero__container {
  height: 90vh;
  background: url(/static/media/hero-bg2.55393ba3.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  align-items: center;
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.028),rgba(0, 0, 0, 0.268));
  z-index: 1;
}
@media screen and (min-width: 1200px) {
  .hero__container {
    background: none;
  }
  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    background: none;
    z-index: 1;
  }
}
.hero__titles--container {
  z-index: 2;
  padding: 2rem;
  /* border: 1px solid #123; */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.hero__main--title {
  font-size: 5rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}
.hero__sub--title {
  font-size: 3rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-align: right;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}

.hero__info--text {
  margin-top: 2rem;
  text-align: left;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.953);
  letter-spacing: 1px;
  font-size: 1rem;
  text-transform: uppercase;
}

.hero__learn-more-btn {
  margin-top: 1rem;
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 1px;
  text-decoration: none;
  cursor: pointer;
  
  border-radius: 20px;
  padding: 0.5rem 0.75rem;
  color: rgb(255, 255, 255);
  background-color: rgb(89, 55, 242);
}
.hero__learn-more-btn:hover {
  color: rgb(255, 255, 255);
  background-color: rgb(118, 0, 0);
  border-radius: 20px;
  padding: 0.5rem 0.75rem;
  color: rgb(58, 42, 42);
  background-color: rgb(255, 255, 255);
}

.sidebar__container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 30;
  /* background-color: #000; */
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
  transform: translate(-100%);
  transition: 0.3s ease-in;
  background: linear-gradient(90deg,rgb(89, 55, 242), rgb(0, 0, 0),rgb(255, 255, 255) );
  background-repeat: no-repeat;
  background-size: 400% 300%;
}
@keyframes gradient {
  0% {
    background-position: 0% 75%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}
.sidebar__top--container {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-size: 1.5rem;
}

.sidebar__logo {
  text-transform: uppercase;
  color: #000;
}

.logo-text-span {
  color:#fff;
}
.close-sidebar__btn {
  transition: 0.3s;
  cursor: pointer;
}
.close-sidebar__btn:hover {
  transform: scale(1.1);
}
.sidebar__links--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 2rem 0;
}
.sidebar--link {
  cursor: pointer;
  padding: 1rem 1rem;
  width: 100%;
  text-align: center;
}
.sidebar--link:hover {
  background-color: rgb(255, 255, 255);
  color: #000;
}
.show-sidebar {
  transform: translate(0);
  animation: gradient 0.5s ease-in;
}

.social__links--container {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
}
.social__link {
  margin: 0.5rem;
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
}
.social__link:hover {
  color: rgb(255, 255, 255);
  transform: scale(1.1);
}
